import { Injectable } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class UtilityService {
  constructor() {}

  formatPriceToNum(price: string): number {
    let symbols = price.split('');

    return +symbols.map(sym => {
      if (!isNaN(+sym)) {
        return sym;
      }
      return '';
    }).join('');
  }

  isObjectEmpty(obj): boolean {
    return !Object.keys(obj).length;
  }
}
